@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
/*Main and SideBar*/

.app {
  display: flex;
  max-width: 1440px;
  margin: 0 auto;
}

/*Main and SideBar mobile view*/

@media (max-width: 960px) {
  .app {
    display: block;
    position: relative;
  }
}
/*Logo*/

.joke-logic__logo {
    display: flex;
    justify-content: space-between;
    padding-bottom: 78px;
}

.joke-logic__logo-title {
    font-weight: bold;
    font-size: 20px;
    line-height: 28px;
}

/*Intro*/

.joke-logic__intro-title {
    font-weight: bold;
    font-size: 32px;
    line-height: 44px;
}

.joke-logic__intro-subtitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 44px;
    margin-bottom: 43px;
}

/*Loading spinner*/

.joke-logic__loader {
    position: absolute;
    top: calc(50vh - 40px);
    left: calc(50vw - 40px);
    width: 80px;
    height: 80px;
}

.joke-logic__loader:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid;
    border-color: #FF6767 transparent #FF6767 transparent;
    -webkit-animation: joke_loader 1.2s linear infinite;
    animation: joke_loader 1.2s linear infinite;
    box-sizing: border-box;
}

@-webkit-keyframes joke-logic__loader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes joke-logic__loader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
/*Rectangle with a joke*/

.joke {
    display: flex;
    flex-direction: column;
    padding: 40px;
    margin-top: 20px;
    background: #F8F8F8;
    border-radius: 20px;
}

/*Favourite icon*/

.joke__like-btn {
    align-self: flex-end;
    border: none;
    cursor: pointer;
    background-color: #F8F8F8;
}

.joke__like-btn-icon {
    color: #FF6767;
}

/*Joke*/

.joke__container {
    display: flex;
    margin-top: 10px;
    background: #F8F8F8;
    border-radius: 20px;
}

/*Comment icon*/

.joke__comment-circle {
    display: flex;
    justify-content: center;
    flex: 0 0 40px;
    overflow: hidden;
    height: 40px;
    margin-right: 20px;
    border-radius: 50%;
    background: white;
}

.joke__comment-circle-icon {
    width: auto;
    height: 22px;
    margin-top: 25%;
    color: #ABABAB;
}

/*Joke content*/

.joke__content {
    width: 100%;
}

/*Joke ID*/

.joke__id {
    color: #ABABAB;
    font-size: 10px;
    line-height: 14px;
}

/*Joke link*/

#joke__id-link {
    font-size: 10px;
    line-height: 14px;
    background: url(data:image/png;base64,) no-repeat 100% 0;
    background-size: 14px 14px;
    padding-right: 19px;
    color: #8EA7FF;
}

/*Joke value*/

.joke__value {
    max-width: 540px;
    margin-top: 5px;
    margin-bottom: 7px;
    font-weight: normal;
    padding-top: 5px;
    padding-bottom: 10px;
    font-size: 18px;
    line-height: 26px;
}

/*Last update and Category*/

.joke__apps {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    font-weight: normal;
    font-size: 10px;
    line-height: 14px;
    color: #ABABAB;
}

/*Joke category*/

.joke__apps-category {
    background: #FFFFFF;
    border-radius: 6px;
    padding: 5px 20px;
    margin-right: -2px;
    font-weight: 700;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #333333;
}

.joke__apps-category:empty {
    display: none;
}

/*Joke mobile view*/

@media (max-width: 1240px) {
    .joke__apps-category {
        padding: 5px 10px;
    }
}

@media (max-width: 480px) {
    .joke__value {
        max-width: calc(100vw - 140px);
    }
}

@media (max-width: 320px) {
    .joke__value {
        max-width: 180px;
        width: 100%;
    }
}
/*Category list*/

.categories {
    display: flex;
    flex-wrap: wrap;
}
/* RadioButton Container */

.joke-options {
    display: flex;
    flex-direction: column;
}

/*Radio buttons takes from here https://codepen.io/hansmaad/pen/zBdzxp
 and here https://dev.to/lkopacz/create-custom-keyboard-accessible-radio-buttons-22eh
*/

.joke-options__form-input {
    display: none;
}

.joke-options__form-input:checked + label:before {
    border-color: #333333;
}

.joke-options__form-input:checked + label:after {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.joke-options__form-label {
    display: inline-block;
    position: relative;
    padding: 0 30px;
    margin-bottom: 15px;
    cursor: pointer;
    font-weight: normal;
    font-size: 18px;
}

.joke-options__form-label:before, :after {
    position: absolute;
    content: '';
    border-radius: 50%;
    transition: all .3s ease;
    transition-property: border-color, -webkit-transform;
    transition-property: transform, border-color;
    transition-property: transform, border-color, -webkit-transform;
}

.joke-options__form-label:before {
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    border: 2px solid #ABABAB;
}

.joke-options__form-label:after {
    top: 5px;
    left: 5px;
    width: 10px;
    height: 10px;
    -webkit-transform: scale(0);
            transform: scale(0);
    background: #333333;
}

*, *:before, *:after {
    box-sizing: border-box;
}

/*Radio button = Search*/

.joke-options__search-form {
    display: block;
    margin-top: 10px;
    margin-bottom: 5px;
}

/*Search input*/

.joke-options__search-input {
    width: 100%;
    padding: 8px 13px;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    border: 2px solid #333333;
    box-sizing: border-box;
    border-radius: 10px;
}

.joke-options__search-input::-webkit-input-placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}

.joke-options__search-input:-ms-input-placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}

.joke-options__search-input::-ms-input-placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}

.joke-options__search-input::placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}

.joke-options__invalid-size{
    display: block;
    margin: 5px 0;
    font-size: 18px;
    color: #FF6767;
}

.joke-options__invalid-value{
    display: block;
    margin: 5px 0;
    font-size: 18px;
    color: #FF6767;
    visibility: visible;
}
/*Get a joke button*/

.joke-btn {
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: white;
    margin-top: 20px;
    padding: 9px 38px;
    background: linear-gradient(92.01deg, #8EA7FF 0%, #7291FF 100%);
    border-radius: 10px;
    border: none;
}

.joke-btn:hover {
    background: linear-gradient(92.01deg, #7291FF 0%, #8EA7FF 100%);
}
/*Button for tablet and mobile view, appeared in top-right corner on 960px screen width*/

.side-button {
    display: none;
}

.side-button__icon {
    cursor: pointer;
    margin-right: 10px;
    background: #333;
    border-radius: 50%;
    padding: 10px 7px;
    width: 28px;
    height: 28px;
    position: relative;
    box-sizing: border-box;
}

.side-button__icon-line{
    display: block;
    width: 14px;
    height: 2px;
    background: #fff;
    border-radius: 2px;
    position: absolute;
}

.first-line{
    top: 10px;
}

.second-line{
    bottom: 10px;
}

.side-button_active .first-line{
    top: 13px;
    transition: 0.1s;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.side-button_active .second-line{
    bottom: 13px;
    transition: 0.1s;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.side-button__title {
    display: none;
}

/*Mobile view*/

@media (max-width: 960px) {
    .side-button {
        display: flex;
        z-index: 4;
        top: 40px;
    }
    .side-button__title {
        display: inline;
        font-size: 20px;
        line-height: 28px;
        color: #ABABAB;
    }
}
/*Content*/

.content {
    flex: 0 1 700px;
    margin: 0 auto;
    padding: 40px 10px;
}

/*Content mobile view*/

@media (max-width: 1240px) {
    .content {
        padding: 40px;
        flex: 1 1 auto;
    }
}

@media (max-width: 960px) {
    .content {
        display: block;
        position: relative;
    }
    .content_deactivate:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
    }
}

@media (max-width: 480px) {
    .content {
        padding: 20px;
    }
}
/*SideBar*/

/*Need to fix*/

.sidebar {
    /*position: fixed;*/
    /*right: 0;*/
    /*top: 0;*/
    /*display: flex;*/
    /*flex-direction: column;*/
    /*width: 480px;*/
    /*height: 100%;*/
    /*overflow-y: auto;*/
    flex: 0 0 480px;
    padding: 40px;
    background: #F8F8F8;
}

/*Need to fix*/

.sidebar_height {
    min-height: 100vh;
}

/*SideBar Favourite heading and jokes*/

.sidebar__title {
    display: inline;
    font-size: 20px;
    line-height: 28px;
    color: #ABABAB;
}

.sidebar__jokes{
    width: 100%;
}

/*SideBar mobile view*/

@media (max-width: 960px) {
    .sidebar {
        display: none;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 3;
    }
    .sidebar__title {
        visibility: hidden;
    }
    .sidebar_active {
        display: block;
        width: 480px;
        min-height: 100vh;
        box-shadow: 0 0 0 9999px rgba(0,0,0, 0.3);
    }
    .sidebar_height {
        min-height: 100vh;
    }

}

@media (max-width: 480px) {
    .sidebar {
        padding: 20px;
        width: 100%;
    }
}


